/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable no-restricted-globals */
/* eslint-disable camelcase */
import { sentenceCase, capitalCase } from 'change-case';
import { useState, useEffect, } from 'react';
import { Link as RouterLink } from 'react-router-dom';
import { useSnackbar } from 'notistack';

// @mui
import { useTheme } from '@mui/material/styles';
import {
  Card,
  Table,
  Avatar,
  Button,
  Checkbox,
  TableRow,
  TableBody,
  TableCell,
  Container,
  Typography,
  TableContainer,
  TablePagination,
  Switch,
  Tabs,
  Tab,
} from '@mui/material';
// swal 
import swal from 'sweetalert';
// format
import FormControlLabel from '@mui/material/FormControlLabel';
// routes
import { PATH_ADMIN } from '../../routes/paths';
// hooks
import useSettings from '../../hooks/useSettings';
// components
import Page from '../../components/Page';
import Label from '../../components/Label';
import Iconify from '../../components/Iconify';
import Scrollbar from '../../components/Scrollbar';
import SearchNotFound from '../../components/SearchNotFound';

import HeaderBreadcrumbs from '../../components/HeaderBreadcrumbs';
// format number 
import { fCurrency, } from '../../utils/formatNumber';
// sections
import { UserListHead, UserListToolbar, UserMoreMenu } from '../../sections/admin/user/list';


// axios

import axios from '../../utils/axios';

// ----------------------------------------------------------------------

const TABLE_HEAD = [
  { id: 'name', label: 'Full Name', alignRight: false },
  { id: 'username', label: 'Username', alignRight: false },
  { id: 'email', label: 'Email', alignRight: false },
  { id: 'phone', label: 'Phone Number', alignRight: false },
  { id: 'pin', label: 'Transaction Pin', alignRight: false },
  { id: 'bal', label: 'Wallet Balance', alignRight: false },
  { id: 'ref', label: 'Earning Balance', alignRight: false },
  { id: 'type', label: 'Account Type', alignRight: false },
  { id: 'ver', label: 'Verification', alignRight: false },
  { id: 'reg_date', label: 'Reg. Date', alignRight: false },
  { id: 'status', label: 'Account Status', alignRight: false },
];
const ACCOUNT_TABS = [
  {
    value: 'ALL',
    label: 'ALL'
  },
  {
    value: 1,
    label: 'Active'
  },
  {
    value: 2,
    label: 'Banned'
  },
  {
    value: 3,
    label: 'Deactivated'
  },
  {
    value: 0,
    label: 'Unverified'

  },
];
// ----------------------------------------------------------------------

export default function AllUserAccount() {
  const theme = useTheme();
  const { themeStretch } = useSettings();
  const [userList, setUserList] = useState([]);
  const [page, setPage] = useState(0);
  const [load, SetLoad] = useState(true);
  const [totalPage, SetTotal] = useState(0);
  const [filterName, setFilterName] = useState('');
  const [rowsPerPage, setRowsPerPage] = useState(5);
  const [dense, Setdense] = useState(false);
  const [role, setRole] = useState('ALL');
  const [selected, setSelected] = useState([]);
  const [isNotFound, SetNotFound] = useState(false);
  const [status, setStatus] = useState('ALL');
  const { enqueueSnackbar } = useSnackbar();
  const AccessToken = window.localStorage.getItem('accessToken');

  useEffect(() => {
    initialize(page, rowsPerPage, status, filterName, role);
  }, []);

  const initialize = async (pag, Habukhan = 5, status, search, role) => {
    let Habukhan_page;
    if (pag > page) {
      Habukhan_page = pag + 1;
    } else {
      Habukhan_page = pag;
    }
    SetLoad(true);
    try {
      const data = await axios.get(`/api/all/user/infomation/admin/setting/${AccessToken}/secure?page=${Habukhan_page}&Habukhan=${Habukhan}&status=${status}&search=${search}&role=${role}`)

      setUserList(data.data?.all_users.data);
      SetTotal(data.data?.all_users?.total)
      SetLoad(false);
      setPage(pag);
      SetNotFound(data.data?.all_users?.total === 0)
    } catch (_) {
      SetLoad(false);
      setUserList([])
    }
  }


  const handleSelectAllClick = (checked) => {
    if (checked.target.checked) {
      const newSelecteds = userList.map((n) => n.username);
      setSelected(newSelecteds);
      return;
    }
    setSelected([]);
  };

  const handleClick = (username) => {
    const selectedIndex = selected.indexOf(username);
    let newSelected = [];
    if (selectedIndex === -1) {
      newSelected = newSelected.concat(selected, username);
    } else if (selectedIndex === 0) {
      newSelected = newSelected.concat(selected.slice(1));
    } else if (selectedIndex === selected.length - 1) {
      newSelected = newSelected.concat(selected.slice(0, -1));
    } else if (selectedIndex > 0) {
      newSelected = newSelected.concat(
        selected.slice(0, selectedIndex),
        selected.slice(selectedIndex + 1),
      );
    }
    setSelected(newSelected);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    initialize(0, event.target.value, status, filterName, role)
    setPage(0);
  };

  const handleFilterByName = async (filterName) => {
    setFilterName(filterName);
    setPage(0);
    if (filterName) {
      initialize(page, rowsPerPage, status, filterName, role);
    } else {
      initialize(page, rowsPerPage, status, filterName, role);
      SetNotFound(false)
      SetLoad(false);
    }
  };

  const handlerole = (role) => {
    setRole(role);
    setPage(0);
    initialize(0, rowsPerPage, status, filterName, role)
  }

  const handleDeleteUser = (userId) => {

    swal({
      title: "Note",
      text: `Are You Sure You Want To Delete this user => ${capitalCase(userId)}`,
      icon: "info",
      buttons: true,
      dangerMode: true,
    })
      .then((willDelete) => {
        if (willDelete) {

          axios.post(`/api/delete/single/record/user/hacker/${AccessToken}/system`, {
            username: userId
          }).then(data => {
            enqueueSnackbar(data.data.message)
            const deleteUser = userList.filter((user) => user.username !== userId);
            setSelected([]);
            setUserList(deleteUser);
          }).catch(error => {
            if (error.data?.message) {
              enqueueSnackbar(error.data.message, { variant: 'error' })
            } else {
              enqueueSnackbar('An Error Occured', { variant: 'error' })
            }

          });
        } else {
          swal("Request Cancelled");
        }
      });

  };

  const handleDeleteMultiUser = (selected) => {
    swal({
      title: "Note",
      text: `Are You Sure You Want To Delete this multiple users => ${selected}`,
      icon: "info",
      buttons: true,
      dangerMode: true,
    })
      .then((willDelete) => {
        if (willDelete) {

          axios.post(`/api/delete/user/record/user/hacker/${AccessToken}/system`, {
            username: selected
          }).then(data => {
            enqueueSnackbar(data.data.message)
            const deleteUsers = userList.filter((user) => !selected.includes(user.username));
            setSelected([]);
            setUserList(deleteUsers);
          }).catch(error => {
            if (error.data?.message) {
              enqueueSnackbar(error.data.message, { variant: 'error' })
            } else {
              enqueueSnackbar('An Error Occured', { variant: 'error' })
            }
          });
        } else {
          swal("Request Cancelled");
        }
      });
  };







  function handleChangeDense(event) {
    Setdense(event.target.checked);
  }

  return (
    <Page title="User: List">
      <Container maxWidth={themeStretch ? false : 'lg'}>
        <HeaderBreadcrumbs
          heading="User List"
          links={[
            { name: 'Dashboard', href: PATH_ADMIN.root },
            { name: 'List' },
          ]}
          action={
            <Button
              variant="contained"
              component={RouterLink}
              to={PATH_ADMIN.user.newUser}
              startIcon={<Iconify icon={'eva:plus-fill'} />}
            >
              New User
            </Button>
          }
        />

        <Card>
          <Tabs
            value={status}
            scrollButtons="auto"
            variant="scrollable"
            allowScrollButtonsMobile
            onChange={(e, value) => { setStatus(value); initialize(page, rowsPerPage, value, filterName, role); }}
          >
            {ACCOUNT_TABS.map((tab) => (
              <Tab disableRipple key={tab.value} label={capitalCase(tab.label)} value={tab.value} />
            ))}
          </Tabs>
          <UserListToolbar
            numSelected={selected.length}
            filterName={filterName}
            onFilterName={handleFilterByName}
            onDeleteUsers={() => handleDeleteMultiUser(selected)}
            onRoleUsers={handlerole}
          />

          <Scrollbar>
            <TableContainer sx={{ minWidth: 800 }}>
              <Table
                size={dense ? 'small' : 'medium'}
              >
                <UserListHead
                  // order={'desc'}
                  // orderBy={'id'}
                  headLabel={TABLE_HEAD}
                  rowCount={userList.length}
                  numSelected={selected.length}
                  // onRequestSort={handleRequestSort}
                  onSelectAllClick={handleSelectAllClick}
                  onDeleteUsers={() => handleDeleteMultiUser(selected)}
                />

                {!load ? <TableBody>
                  {userList.map((row) => {
                    const { id, name, username, email, phone, pin, bal, refbal, type, kyc, status, profile_image, date } = row;
                    const isItemSelected = selected.indexOf(username) !== -1;
                    return (
                      <TableRow
                        hover
                        key={id}
                        tabIndex={-1}
                        role="checkbox"
                        selected={isItemSelected}
                        aria-checked={isItemSelected}
                      >
                        <TableCell>
                          <Checkbox checked={isItemSelected} onClick={() => handleClick(username)} />
                        </TableCell>

                        <TableCell sx={{ display: 'flex', alignItems: 'center' }}>
                          <Avatar alt={name} src={profile_image} sx={{ mr: 2 }} />
                          <Typography variant="subtitle2" noWrap>
                            {name}
                          </Typography>
                        </TableCell>
                        <TableCell align="left">{username}</TableCell>
                        <TableCell align="left">{email}</TableCell>
                        <TableCell align="left">{phone}</TableCell>
                        <TableCell align="left">{pin}</TableCell>
                        <TableCell align="left">₦{fCurrency(bal)}</TableCell>
                        <TableCell align="left">₦{fCurrency(refbal)}</TableCell>
                        <TableCell align="left">{type}</TableCell>

                        <TableCell align="left">{kyc === 1 && 'Yes' || kyc === 2 && 'processing' || kyc === 0 && 'NO'}</TableCell>
                        <TableCell align="left">{date}</TableCell>
                        <TableCell align="left">
                          <Label
                            variant={theme.palette.mode === 'light' ? 'ghost' : 'filled'}
                            color={(status === 1 ? 'success' : 'error')}
                          >
                            {sentenceCase((status === 0 && 'unverified') || (status === 1 && 'active') || (status === 2 && 'banned') || (status === 3 && 'deactivated'))}
                          </Label>
                        </TableCell>

                        <TableCell align="right">
                          <UserMoreMenu onDelete={() => handleDeleteUser(username.toString())} userName={id.toString()} />
                        </TableCell>
                      </TableRow>
                    );
                  })}
                </TableBody> : <TableRow>
                  <TableCell align="center" colSpan={5} sx={{ py: 3 }}>
                    <Typography variant="body2">Loading ...</Typography>
                  </TableCell>
                </TableRow>}
                {isNotFound && (
                  <TableRow>
                    <TableCell align="center" colSpan={5} sx={{ py: 3 }}>
                      <SearchNotFound searchQuery={filterName} />
                    </TableCell>
                  </TableRow>
                )}
              </Table>
            </TableContainer>
          </Scrollbar>

          <FormControlLabel
            control={<Switch checked={dense} onChange={(event) => handleChangeDense(event)} />}
            label="Dense padding" align="left"
          />

          <TablePagination
            rowsPerPageOptions={[5, 10, 25, 100]}
            component="div"
            count={totalPage}
            rowsPerPage={rowsPerPage}
            page={page}
            onPageChange={(e, page) => initialize(page, rowsPerPage, status, filterName, role)}
            onRowsPerPageChange={handleChangeRowsPerPage}
          />

        </Card>
      </Container>
    </Page>
  );
}

// ----------------------------------------------------------------------

